import { SvgIcon, styled } from "@rp/react/web/mui/core"
import {
  AccountBalance,
  AltRoute,
  Apartment,
  AttachMoney,
  Audiotrack,
  Biotech,
  Bolt,
  BugReport,
  BusinessCenter,
  CalendarToday,
  Category,
  CategorySharp,
  ColorLens,
  Computer,
  Construction,
  CorporateFare,
  EmojiPeople,
  EmojiSymbols,
  Euro,
  Flag,
  Gavel,
  Grass,
  InsertChart,
  JoinInner,
  Language,
  LegendToggle,
  Lightbulb,
  LocationOn,
  ManageSearch,
  MedicalServices,
  MonetizationOn,
  Museum,
  NaturePeople,
  NextPlan,
  NightsStay,
  Payments,
  Pentagon,
  PeopleAlt,
  Person,
  PersonPin,
  Pets,
  PrecisionManufacturing,
  Public,
  School,
  ShowChart,
  Source,
  Spa,
  SportsFootball,
  TagFaces,
  Thermostat,
  TimerOutlined,
  TravelExplore,
} from "@rp/react/web/mui/icons"
import { SearchesFilled } from "./Icon"
import Commodity from "./assets/icons/entities/commodity.svg"
import Element from "./assets/icons/entities/element.svg"
// Assets
import Fruits from "./assets/icons/entities/fruits.svg"
import Handshake from "./assets/icons/entities/handshakeMUI.svg"
import Sustainability from "./assets/icons/entities/sustainability.svg"
import Topic from "./assets/icons/entities/topic.svg"
import WatchlistsFilled from "./assets/icons/watchlists-filled.svg"

export const IconEntity = ({ icon, className }: { icon: string; className?: string }) => {
  switch (icon) {
    case "ANML": //  animal
      return <Pets className={className} />
    case "ACTN": //  action
      return <NextPlan className={className} />
    case "ASTR": // astronomy
      return <NightsStay className={className} />
    case "BUSI": // business
      return <BusinessCenter className={className} />
    case "CHAR": // character
      return <EmojiPeople className={className} />
    case "COLR": // color
      return <ColorLens className={className} />
    case "CMDT": // commodity
      return <Commodity className={className} />
    case "COMP": // company
      return <Apartment className={className} />
    case "CEPT": // concept
      return <Lightbulb className={className} />
    case "CURT": // currency type
      return <MonetizationOn className={className} />
    case "CURR": // currency
      return <AttachMoney className={className} />
    case "ECON": // economy
      return <Payments className={className} />
    case "ELEM": // element
      return <Element className={className} />
    case "EMOT": // emotion
      return <TagFaces className={className} />
    case "ETF": // ETF
      return <LegendToggle className={className} />
    case "ETHN": // ethnicity
      return <Handshake className={className} />
    case "EVNT": // event
      return <Bolt className={className} />
    case "topics": // topics
      return <Bolt className={className} />
    case "TOPC": // topics
      return <Bolt className={className} />
    case "rp_topic": // topics
      return <Bolt className={className} />
    case "FCTY": // facility
      return <Construction className={className} />
    case "FINC": // finance
      return <AccountBalance className={className} />
    case "FRTS": // fruits
      return (
        <SvgIcon className={className}>
          <Fruits />
        </SvgIcon>
      )
    case "INSE": // insect
      return <BugReport className={className} />
    case "INRT": // interest-rate
      return <ShowChart className={className} />
    case "LAND": // landmark
      return <Museum className={className} />
    case "LANG": // languages
      return <Language className={className} />
    case "language": // languages
      return <Language className={className} />
    case "LAWS": // laws
      return <Gavel className={className} />
    case "macroSearch": // macro search
      return null // TODO pending to define the icon
    case "MDCO": // medical
      return <MedicalServices className={className} />
    case "MSIC": // music
      return <Audiotrack className={className} />
    case "NATL": // nationality
      return <Flag className={className} />
    case "OBJT": // object
      return <Category className={className} />
    case "ORGA": // organization
      return <CorporateFare className={className} />
    case "ORGT": // organization Type
      return <CorporateFare className={className} />
    case "OTHR": // other
      return <AltRoute className={className} />
    case "PEOP": // person
      return <Person className={className} />
    case "PHYS": // physics
      return <NaturePeople className={className} />
    case "PLCE": // place
      return <LocationOn className={className} />
    case "PLNT": // plant
      return <Grass className={className} />
    case "PLTC": // politics
      return <School className={className} />
    case "POFY": // profanity
      return <EmojiSymbols className={className} />
    case "POST": // position
      return <PersonPin className={className} />
    case "PRDT": // product-type
      return <CategorySharp className={className} />
    case "PROD": // product
      return <CategorySharp className={className} />
    case "recent": // recent search
      return <TimerOutlined className={className} />
    case "REGN": // region
      return <TravelExplore className={className} />
    case "SCIE": // science
      return <Biotech className={className} />
    case "SESO": // season
      return <CalendarToday className={className} />
    case "SECT": // sector
      return <PrecisionManufacturing className={className} />
    case "SECU": // securities
      return <Euro className={className} />
    case "SCTY": // securities-types
      return <Euro className={className} />
    case "savedSearch": //saved search
      return <SearchesFilled className={className} />
    case "sentence": // sentence
      return <JoinInner className={className} />
    case "SHPE": // shape
      return <Pentagon className={className} />
    case "SOCI": // social
      return <Public className={className} />
    case "SRCE": // source
      return <Source className={className} />
    case "source": // source
      return <Source className={className} />
    case "SPOR": // sport
      return <SportsFootball className={className} />
    case "STAT": // statistics
      return <InsertChart className={className} />
    case "SUST": // sustainability
      return (
        <SvgIcon className={className}>
          <Sustainability />
        </SvgIcon>
      )
    case "TEAM": // team
      return <PeopleAlt className={className} />
    case "TECH": // technology
      return <Computer className={className} />
    case "smartTopic": // topic
      return (
        <SvgIcon className={className}>
          <Topic />
        </SvgIcon>
      )
    case "VEGT": // vegetable
      return <Spa className={className} />
    case "watchlist": // watchlist
      return <SvgIcon className={className} component={WatchlistsFilled} inheritViewBox />
    case "WTHR": // weather
      return <Thermostat className={className} />
    case "keyword":
      return <ManageSearch className={className} />
    //MACRO SEARCHES
    case "macro_companies":
      return <Apartment className={className} />
    case "macro_organizations":
      return <CorporateFare className={className} />
    case "macro_people":
      return <Person className={className} />
    case "macro_places":
      return <LocationOn className={className} />
    case "macro_products":
      return <CategorySharp className={className} />
    case "macro_concepts":
      return <Lightbulb className={className} />
    case "macro_sources":
      return <Source className={className} />
    case "macro_topics":
      return <Bolt className={className} />
    default:
      return null
  }
}

export const QueryIcon = styled(IconEntity)(({ theme }) => ({
  color: theme.palette.primary.contrastText,
  width: theme.spacing(2),
  height: theme.spacing(2),
  marginLeft: "10px !important",
  marginRight: "-2px !important",
}))

